@use "base/variables" as v;
@use "base/mixins" as m;

.navegacion {
	display: flex;
	flex-direction: column;
	@include m.desktop {
		flex-direction: row;
	}

	&__enlace {
		color: v.$blanco;
		font-size: 2rem;
		padding: 2rem;
		font-weight: v.$bold;
		text-transform: uppercase;
		text-align: center;
		transition-property: background-color;
		transition-duration: 0.3s;

		&--actual,
		&:hover {
			background-color: v.$blanco;
			color: v.$azul;
		}
	}
}
